import React, { useState, useEffect } from 'react';
import StepBar from '../../components/StepBar/StepBar';
import MaterialLibrary from '../../components/MaterialLibrary/MaterialLibrary';
import SketchBox from '../../components/SketchBox/SketchBox';
import './Makeunit.css';

const Makeunit = (props) => {
  const { currentStep, handleNextStep, handlePrevStep, selectedMaterials, onAddMaterial, onSaveSketch } = props;
  const [targets, setTargets] = useState(() => {
    const savedTargets = sessionStorage.getItem('sketchTargets');
    return savedTargets ? JSON.parse(savedTargets) : [];
  });

  useEffect(() => {
    sessionStorage.setItem('sketchTargets', JSON.stringify(targets));
  }, [targets]);

  useEffect(() => {
    renderCanvas();
  }, [targets]); // �������а���targets������ÿ��targets����ʱ�����ᴥ����useEffect

  function renderCanvas() {
    const sketchBox = document.querySelector('.sketch-box');
    if (!sketchBox) return; // ȷ��sketchBox�Ѿ�����Ⱦ

    const canvas = document.createElement('canvas');
    const ctx = canvas.getContext('2d');

    // canvas.width = sketchBox.offsetWidth;
    // canvas.height = sketchBox.offsetHeight;

    canvas.width = 512;
    canvas.height = 512;

    const drawImageOnCanvas = (target) => new Promise((resolve) => {
      const img = new Image();
      img.src = target.src;
      img.onload = () => {
        ctx.save();
        ctx.translate(target.x + target.width / 2, target.y + target.height / 2);
        ctx.rotate(target.rotate * Math.PI / 180);
        ctx.drawImage(img, -target.width / 2, -target.height / 2, target.width, target.height);
        ctx.restore();
        resolve();
      };
    });

    Promise.all(targets.map(drawImageOnCanvas)).then(() => {
      const dataURL = canvas.toDataURL('image/png', 1);
      onSaveSketch(dataURL); // ��dataURL����������������������
    });
  }

  return (
      <div className="container">
        <div className="left-side">
          <StepBar currentStep={currentStep} handleNextStep={handleNextStep} handlePrevStep={handlePrevStep} onSavePattern={renderCanvas} />
          <div className="middle">
            <SketchBox targets={targets} setTargets={setTargets} />
          </div>
          <div className="bottom"></div>
        </div>
        <div className="right-side">
          <MaterialLibrary materials={selectedMaterials} onAddMaterial={onAddMaterial} />
        </div>
      </div>
  );
};

export default Makeunit;
